

import Vue from 'vue';
import FastClick from 'fastclick';
import App from './App';
import router from './router';
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import store from './store'

FastClick.attach(document.body);

Vue.config.productionTip = false;

Vue.component('flexbox', Flexbox);
Vue.component('flexbox-item', FlexboxItem);


Vue.use(ToastPlugin);
Vue.use(ConfirmPlugin);
Vue.use(AlertPlugin);
Vue.use(LoadingPlugin);
Vue.prototype.showToast = function (text, type) {
this.$vux.toast.show({
text: text,
type: type || 'success'
})
};
Vue.prototype.showAlert = function (content, title) {
this.$vux.alert.show({
title: title || '提示',
content: content,
hideOnBlur: true
})
};
Vue.prototype.showConfirm = function (title, onConfirm) {
this.$vux.confirm.show({
title: title,
onConfirm () {
onConfirm()
}
})
};
Vue.prototype.showLoading = function (show = true) {
const visible = this.$vux.loading.isVisible();
if (show) {
if (!visible) {
this.$vux.loading.show()
}
} else {
if (visible) {
this.$vux.loading.hide()
}
}
};
Vue.prototype.errMsg = function (text) {
this.$vux.toast.show({
text: text,
type: 'cancel'
})
};

/* eslint-disable no-new */
const app = new Vue({
el: '#app-box',
router,
store,
components: { App },
template: '<App/>'
});

export default app;



