
import Layout from '../Layout'
import Cell from 'vux/src/components/cell/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { random } from '../../utils'

export default {
components: {
Layout,
Card,
Cell,
Group,
XButton,
XNumber,
XInput
},
data () {
return {
num: 1,
ignoreRed: '',
ignoreBlue: '',
redPool: [],
bluePool: [],
result: [],
first: true
}
},
created () {
this.reset()
},
methods: {
reset () {
let red = [];
let blue = [];
const ignoreRed = this.ignoreRed.split(',');
const ignoreBlue = this.ignoreBlue.split(',');
for (let i = 1; i <= 33; i++) {
if (ignoreRed.indexOf(i.toString()) === -1) {
red.push(i)
}
}
for (let i = 1; i <= 16; i++) {
if (ignoreBlue.indexOf(i.toString()) === -1) {
blue.push(i)
}
}
this.redPool = red;
this.bluePool = blue;
},
submit () {
this.result = [];
for (let i = 0; i < this.num; i++) {
this.reset();
let each = {
red: [],
blue: []
};
while (each.red.length < 6) {
each.red.push(this.redPool.splice(random(0, this.redPool.length - 1), 1)[0]);
}
each.red = each.red.sort(function (a, b) {
return a - b
});
each.blue.push(this.bluePool.splice(random(0, this.bluePool.length - 1), 1)[0]);
this.result.push(each)
}
this.first = false
}
}
}



