
import Icon from 'vux/src/components/icon/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'Layout',
directives: {
TransferDom
},
components: {
Icon,
Group,
ViewBox,
XButton,
XDialog,
XHeader
},
props: {
showHeader: {
type: Boolean,
default: true
},
showBack: {
type: Boolean,
default: true
},
title: {
type: String,
default: ''
},
showIcon: {
type: Boolean,
default: false
},
rightIcon: {
type: String,
default: 'search'
}
},
data () {
return {
showDialog: false,
headerHeight: '',
bottomHeight: ''
}
},
mounted () {
this.$nextTick(() => {
this.headerHeight = this.$refs.header.offsetHeight + 'px';
this.bottomHeight = this.$refs.footer.offsetHeight + 'px'
})
},
methods: {
search () {
this.$emit('search-do');
this.showDialog = false
}
}
}



